.c-table01 {
  border-collapse: collapse;
  border-spacing: 0;
  margin: 0;
  display: table;
  width: 100%;
  &__row {
    border-bottom: 1px solid #eaeaea;
    border-top: 1px solid #eaeaea;
    display: table-row;
  }
  &__heading {
    width: 31.7%;
    padding: 5px 0 5px 10px;
    font-weight: 700;
    @include font-line(12, 15);
    text-align: left;
    vertical-align: middle;
    display: table-cell;
    position: relative;
    border-bottom: 1px solid #fff;
    border-top: 1px solid #fff;
    background-color: #eaeaea;
  }
  &__data {
    width: 68.3%;
    padding: 5px 15px 10px 15px;
    font-weight: 500;
    @include font-line(12, 15);
    text-align: left;
    display: table-cell;
  }
}

