.c-table02 {
  border-collapse: collapse;
  border-spacing: 0;
  margin: 0;
  display: table;
  width: 100%;
  &__row {
    border-bottom: 1px dotted #aaaaaa;
    display: table-row;
  }
  &__heading {
    width: 18%;
    padding: 9px 0;
    font-weight: 700;
    @include font-line(12, 15);
    text-align: left;
    vertical-align: top;
    display: table-cell;
    position: relative;
  }
  &__data01 {
    width: 82%;
