.c-table03 {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  margin: 0;
  display: table;
  &__row {
    border-bottom: 5px solid #fff;
    background-color: #f2f2f2;
    margin: 0 0 10px;
    display: table-row;
    &:last-child {
      border-bottom: none;
      margin-bottom: 0;
    }
  }
  &__heading {
    width: 92px;
    padding: 9px 5px 9px 10px;
    font-weight: 700;
    @include font-line(13, 18);
    text-align: left;
    vertical-align: top;
    display: table-cell;
    position: relative;
  }
  &__data {
    padding: 9px 10px 9px 0;
    font-weight: 500;
    @include font-line(14, 18);
    text-align: left;
    display: table-cell;
  }
}

