.c-table04 {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  margin: 0;
  display: table;
  &__row {
    border-bottom: 1px dashed #aaa;
    display: table-row;
  }
  &__heading {
    background-color: #fff;
    width: 77px;
    padding: 5px 0;
    font-weight: 700;
    @include font-line(12, 14);
    text-align: left;
    vertical-align: middle;
    display: table-cell;
    position: relative;
    i {
      width: 15px;
      margin-right: 5px;
      @include font-line(14, 14);
      text-align: center;
      position: relative;
      top: 2px;
    }
  }
  &__data {
    background-color: #fff;
    padding: 5px 0;
    font-weight: 500;
    @include font-line(14, 14);
    text-align: left;
    vertical-align: middle;
    display: table-cell;
  }
}
