// 使用例
// <blockquote class="c-blockquote01">
//     サンプルテキスト
//     <span class="c-blockquote01__src">引用元</span>
// </blockquote>
.c-blockquote01 {
  border: 2px solid #dcdcdc;
  border-radius: 8px;
  margin: 0;
  padding: 15px 60px 40px 40px;
  font-style: italic;
  @include font-line(10, 15);
  color: $color-text01;
  position: relative;
  &__src {
    font-style: italic;
    @include font-line(10, 10);
    text-align: right;
    position: absolute;
    right: 20px;
    bottom: 15px;
    &::before {
      content: "―";
    }
  }
  &::before {
    content: "”";
    font-style: normal;
    @include font-line(36, 45);
    color: #fff;
    display: inline-block;
    position: absolute;
    top: 15px;
    left: 7px;
    width: 27.5px;
    height: 27.5px;
    border-radius: 50%;
    background-color: #dcdcdc;
    text-align: center;
  }
}
