body {
	color: $color-text01;
	font-family: $font-family-base;
	font-size: #{$font-size-base}px;
	font-weight: $font-weight-base;
	line-height: $line-height-base;
	margin: 0;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	word-wrap: break-word;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

ul,
li {
  list-style: none;
}

h1,h2,h3,h4,h5,h6 {
	margin: 0 auto;
}

img {
  vertical-align: middle;
}

main {
  a {
		border-bottom: 1px solid #004ea2;
    padding: 0 0 1px;
    color: #004ea2;
    text-decoration: none;
    display: inline-block;
    position: relative;
  }
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

ul,
li {
	list-style: none;
}

img {
	vertical-align: middle;
	max-width: 100%;
}

textarea {
	@include font-line(10, 15);
}

input[type=text],
input[type=tel],
input[type=url],
input[type=email],
input[type=password],
input[type=search],
input[type=number],
select {
	@include font-line(12, 30);
}
button {
	padding: 0 0.2em;
  font-family: $font-family-base;
}
