.p-step {
  &Description {
    margin: 0 10px 20px 10px;
    @include font-line(14, 18);
  }
  &Box {
    $step: &;
    border: 1px solid #494440;
    border-radius: 3px;
    margin-bottom: 20px;
    padding: 10px 0 10px 55px;
    position: relative;
    &__title {
      @include font-line(14, 14);
      display: flex;
      align-items: center;
      margin: 0;
      min-height: 25px;
      padding: 0 10px;
      position: relative;
      #{$step}__seal {
        @include font-line(10, 10);
        font-weight: 700;
        background-color: $color-primary;
        border-radius: 50%;
        color: #fff;
        display: flex;
        align-items: center;
        flex-flow: column wrap;
        justify-content: center;
        height: 47.5px;
        width: 47.5px;
        position: absolute;
        bottom: -29px;
        left: -46px;
        em {
          @include font-line(20, 20);
          font-style: normal;
        }
      }
      #{$step}__titletext {
        flex: auto;
      }
    }
    &__body {
      @include font-line(12, 16);
      margin-top: 5px;
      padding: 0 10px;
      border-top: 1px solid #494440;
      .days {
        font-weight: 700;
      }
    }
  }
}
