.c-seal01 {
  background-color: $color-main;
  margin: 0 10px 0 0;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  float: left;

  &:last-child {
    margin-right: 0;
  }

  &--l {
    width: 55px;
    height: 15px;
    @include font-line(8, 8);
  }
  &--m1 {
    width: 35px;
    height: 13px;
    @include font-line(7, 7);
  }
  &--m2 {
    width: 55px;
    height: 13px;
    @include font-line(7, 7);
  }
  &--s1 {
    width: 30px;
    height: 10px;
    @include font-line(6, 6);
  }
  &--s2 {
    width: 25px;
    height: 13px;
    @include font-line(6, 6);
  }
}

.c-seal02 {
  background-color: #fff;
  border: 1px solid $color-text01;
  margin: 0 5px 0 0;
  color: $color-text01;
  display: flex;
  justify-content: center;
  align-items: center;
  float: left;

  &:last-child {
    margin-right: 0;
  }

  &--l {
    width: 55px;
    height: 15px;
    @include font-line(8, 8);
  }
  &--m {
    width: 55px;
    height: 16px;
    @include font-line(10, 10);
  }
  &--s {
    width: 30px;
    height: 10px;
    @include font-line(6, 6);
  }
}
