/* background - utility
--------------------------------------------------------- */
.u-bg-none {
  background: none !important;
}
/* bgcolor */
.u-bgc-none {
  background-color: transparent !important;
}

.u-bgc-primary {
  background-color: $color-primary;
}

.u-bgc-secondary {
  background-color: $color-secondary;
}

.u-bgc-tertiary {
  background-color: $color-tertiary;
}

.u-bgc-quaternary {
  background-color: $color-quaternary;
}

