.c-heading {
  // 大見出し01
  // 使用例: <h2 class="c-heading01"><span class="c-heading01__inner">見出し</span><span class="c-heading01__sub">MIDASHI</span></h2>
  &01 {
    @include font-line(22.5, 22.5);
    font-weight: 700;
    margin: 0 auto 24px;
    padding: 0 0 10px 0;
    border: none;
    text-align: center;
    &__inner {
      display: block;
    }
    &__sub {
      @include font-line(13, 6.5);
      font-family: "M PLUS Rounded 1c";
      font-weight: 700;
      padding-top: 7px;
      border-bottom: 4px solid $color-sub;
      display: inline-block;
    }
  }

  // 中見出し01
  // 使用例: <h3 class="c-heading02">中見出し01</h3>
  &02 {
    border-bottom: 2px solid #494440;
    margin: 0 auto 20px;
    padding: 0 0 10px;
    font-weight: 700;
    @include font-line(12.5, 12.5);
    color: $color-text01;
    text-align: left;
    display: block;
  }

  // 中見出し02
  // 使用例: <h3 class="c-heading03">中見出し02</h3>
  &03 {
    border-bottom: 1px solid #bababa;
    height: 15px;
    margin: 0 0 17.5px;
    padding: 0 0 17.5px 7.5px;
    font-weight: 700;
    @include font-line(12, 15);
    color: $color-text01;
    text-align: left;
    position: relative;
    &::before {
      content: '';
      border-left: 3.5px solid $color-primary;
      height: 15px;
      position: absolute;
      left: 0;
    }
  }

  // 小見出し01
  // 使用例: <h4 class="c-heading05">小見出し01</h4>
  &04 {
    background-color: #b2e2f8;
    height: 22.5px;
    margin: 0 0 17.5px;
    padding: 0 0 0 5px;
    font-weight: 700;
    @include font-line(10, 22.5);
    color: $color-text01;
    text-align: left;
  }
}
