/* border - utility
--------------------------------------------------------- */

/* border-color */
.u-bc-base {
  border-color: $color-border;
}


/* border-radius */
@for $value from 1 to 16 {
  .u-br-#{$value} {
    border-radius: #{$value}px;
  }
}


/* border-none */

.u-bd-0 {
  border: 0;
}

.u-bd-t-0 {
  border-top: 0;
}

.u-bd-r-0 {
  border-right: 0;
}

.u-bd-b-0 {
  border-bottom: 0;
}

.u-bd-l-0 {
  border-left: 0;
}
