.c-read01 {
  width: 100%;
  font-weight: 700;
  @include font-line(15, 18);
}

.c-read02 {
  width: 100%;
  font-weight: 700;
  @include font-line(13, 16);
}

.c-text01 {
  width: 100%;
  font-weight: 500;
  @include font-line(10, 17);
  strong {
    background-color: #fff996;
    font-weight: 700;
  }
}

.c-annotation {
  margin: 0 0 15px;
  padding: 0 0 0 26px;
  @include font-line(10, 15);
  position: relative;
  &__number {
    @include font-line(10, 15);
    position: absolute;
    top: 0;
    left: 0;
    &::before {
      content: "※";
    }
  }
}
