.l-breadcrumb {
  margin: 0 0 20px;
  padding: 0;
  text-align: right;
  &__item {
    $this: &;
    @include font-line(7.5, 7.5);
    display: inline-block;
    vertical-align: middle;
    + #{$this} {
      &::before {
        content: ">";
      }
    }
    a {
      border: none;
      color: $color-text01;
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}