.p-pagination {
  font-size: 0;
  margin: 7px 0 10px;
  padding: 0;
  &__item {
    $item: &;
    background-color: #eaeaea;
    width: 20px;
    height: 20px;
    margin-bottom: 5px;
    @include font-line(10, 10);
    color: $color-text01;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    + #{$item} {
      margin-left: 10px;
    }
    &:first-child,
    &:last-child {
      width: 35px;
    }
    &.is-active {
      background-color: #494440;
      color: #fff;
    }
    a {
      border: none;
      width: 100%;
      height: 100%;
      color: $color-text01;
      text-decoration: none;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
