.l-header {
  position: relative;
  z-index: 10;
  &__inner {
    display: flex;
    justify-content: space-between;
    margin: 0;
    position: relative;
  }
  &__left {
    flex: 0 0 65px;
    margin: 0;
    padding: 7.5px 0 9px 10px;
  }
  &__center {
    flex: auto;
    padding: 7.5px 0 9px;
  }
  &__top {
    margin: 0 0 5px;
    @include font-line(10, 10);
    color: #484848;
  }
  &__logo {
    width: 52.5px;
    height: 52.5px;
    margin: 0;
    padding: 0;
    a {
      width: 100%;
      height: 100%;
      display: block;
    }
    img {
      width: 100%;
      height: auto;
      display: block;
    }
  }
  &__right {
    display: flex;
    flex: 0 0 105px;
    padding: 7.5px 0 9px;
    .tel {
      @include font-line(26, 42);
      background-color: $color-primary;
      color: #fff;
      height: 52px;
      width: 52px;
      text-align: center;
      border: none;
      border-radius: 50%;
      padding: 7px;
    }
    .menuBtn {
      background: #fff;
      border: none;
      height: 52px;
      flex: auto;
      &:focus {
        outline: none;
      }
      i {
        @include font-line(28, 28);
      }
      .inner {
        @include font-line(10, 10);
        font-family: 'Roboto',sans-serif;
        font-weight: 900;
        display: block;
        margin-top: 4px;
      }
    }
    a {
      text-decoration: none;
    }
  }
  &__balloon {
    width: 112px;
    height: 36px;
    border: 1px solid $color-sub;
    border-radius: 10px;
    background: #fffaf3;
    margin: 6px 0 0 10px;
    padding: 0 5px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    &::before ,
    &::after {
      content: "";
      position: absolute;
      border-top: 5px solid transparent;
      border-bottom: 5px solid transparent;
      bottom: 11px;
    }
    &::before {
      border-right: 7px solid $color-sub;
      left: -6px;
    }
    &::after {
      border-right: 7px solid #fffaf3;
      left: -4px;
    }
    .text {
      font-weight: 500;
      @include font-line(10, 15);
      display: block;
      position: relative;
      strong {
        color: $color-sub;
        font-family: 'Roboto', sans-serif;
        font-weight: 700;
        @include font-line(17.5, 17.5);
        letter-spacing: -0.01em;
      }
    }
  }
}

.l-gNavi {
  $gnavi: &;
  width: 100%;
  position: relative;
  z-index: 2;
  border-top: 1px solid #9d9d9d;
  border-bottom: 1px solid #9d9d9d;
  &__list {
    display: flex;
    height: 50px;
    margin: 0;
    padding: 0;
  }
  &__item {
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    flex: 1 0 18%;
    height: 100%;
    vertical-align: middle;
    font-weight: 500;
    text-align: center;
    position: relative;
    border-left: 1px solid #c4c4c4;
    background: #fffaf3;
    &:first-child {
      border-left: none;
    }
    .inner {
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      color: $color-text01;
      text-decoration: none;
      display: flex;
      flex-flow: column wrap;
      align-items: center;
      justify-content: center;
      position: relative;
      i, .ja {
        display: block;
      }
      i {
        margin: 0 0 4.5px;
        @include font-line(18, 18);
      }
      .ja {
        @include font-line(10, 10);
        font-weight: 600;
      }
    }
  }
  @mixin gnavi-item-current {
    .inner {
      color: $color-main;
    }
  }
  &.is-current {
    @include gnavi-item-current;
  }
  @at-root .page-estate {
    #gnavi-estate {
      @include gnavi-item-current;
    }
  }
  @at-root .page-contact {
    #gnavi-contact {
      @include gnavi-item-current;
    }
  }
  @at-root .page-company {
    #gnavi-company {
      @include gnavi-item-current;
    }
  }
  @at-root .page-favorite {
    #gnavi-favorite {
      @include gnavi-item-current;
    }
  }
}
