.c-checkbox {
  $checkbox: &;
  display: inline-block;
  &__input {
    display: none;
    &:checked + #{$checkbox}__parts {
      &::after {
        @include font-line(16);
        content: $if-check;
        color: #333;
        font: normal normal normal 16px/1 "myiconfont";
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        position: absolute;
        bottom: 0;
        left: 1px;
        margin: auto;
        z-index: 1;
      }
    }
  }
  &__parts {
    @include font-line(13, 19);
    padding-left: 25px;
    position: relative;
    display: inline-block;
    &::before {
      background: #fff;
      border: 1px solid #333;
      content: "";
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      height: 18px;
      width: 18px;
      margin: auto;
    }
  }
}

.c-radiobox {
  $radiobox: &;
  display: inline-block;
  &__input {
    display: none;
    &:checked + #{$radiobox}__parts {
      &::before {
        opacity: 1;
      }
    }
  }
  &__parts {
    padding: 0 0 0 25px;
    @include font-line(15, 22);
    display: inline-block;
    position: relative;
    cursor: pointer;
    &::before,
    &::after {
      content: "";
      margin: auto;
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
    }
    // チェックマーク
    &::before {
      background-color: #666;
      border-radius: 50%;
      width: 12px;
      height: 12px;
      left: 4px;
      opacity: 0;
      z-index: 2;
    }
    // チェックボックス
    &::after {
      background-color: #ececec;
      border: 1px solid #838383;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      left: 0;
      z-index: 1;
    }
  }
}

.c-selectbox {
  background: #fff url(../../img/bg_select.png) no-repeat top right;
  background-size: 20px 28px;
  border: 1px solid #838383;
  border-radius: 5px;
  width: 100%;
  height: 30px;
  padding: 0 20px 0 10px;
  font-weight: 400;
  vertical-align: top;
  appearance: none;
  cursor: pointer;
  &::-ms-expand {
    display: none;
  }
}

.c-textbox {
  background-color: #fff;
  border: 1px solid #838383;
  border-radius: 5px;
  width: 100%;
  height: 30px;
  padding-left: 10px;
}

.c-textarea {
  background-color: #fff;
  border: 1px solid #838383;
  border-radius: 5px;
  width: 100%;
  margin: 0;
  padding: 10px;
}
