.c-card {
  background-color: #fff;
  border: 1px solid #a0a0a0;
  flex: 0 0 49%;
  text-align: center;
  position: relative;
  border-radius: 5px;
  overflow: hidden;
  a {
    border: none;
    margin: 0;
    padding: 0;
    text-decoration: none;
  }
  &__wrap {
    display: block;
    color: $color-text01;
    text-decoration: none;
  }
  &__photo {
    width: 100%;
    height: 100%;
    border-bottom: 1px solid #a0a0a0;
    position: relative;
    padding: 75% 0 0;
    img {
      height: 100%;
      width: 100%;
      object-fit: contain;
      font-family: 'object-fit: contain;';
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
    }
  }
  &__ribbon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    @include font-line(10, 10);
    font-family: 'Roboto',sans-serif;
    font-weight: 900;
    color: #fff;
    background: $color-main;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 6px;
    left: 6px;
    z-index: 2;
    letter-spacing: -0.02em;
    &--new {
    }
    &--pricedown {
    }
    &--sold,
    &--nego {
      width: 58px;
      height: 58px;
      background-color: #000;
      @include opacity(0.6);
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
    }
  }

  &__body {
    padding: 4px;
    height: 86px;
    &__top {
      text-align: center;
    }
  }
  &__type {
    background: #494440;
    width: 60px;
    height: 20px;
    font-weight: 500;
    @include font-line(10, 10);
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 5px;
    right: 5px;
    z-index: 1;
  }
  &__price {
    font-weight: 900;
    font-family: 'Roboto',sans-serif;
    @include font-line(20, 20);
    color: $color-sub;
    .sub {
      font-family: 'Noto Sans Japanese',sans-serif;
      font-weight: 700;
      @include font-line(10, 10);
      color: $color-text01;
    }
  }

  &__info {
    text-align: center;
  }
  &__infoBottom {
    @include font-line(10, 10);
    text-align: center;
    border-top: 1px solid #a0a0a0;
    padding-right: 4px;
    height: 17px;
  }
  &__date {
    @include font-line(10, 15);
  }
  &__addr {
    @include font-line(12, 18);
    font-weight: 700;
    height: 45px;
  }
  &__size {
    @include font-line(11, 11);
  }
}

