.slick-slide {
  img {
    margin: 0 auto;
  }
}
.slick-arrow {
  @include font-line(15, 40);
  background: rgba(0, 0, 0, .7);
  border: none;
  border-radius: 50%;
  color: #fff;
  height: 40px;
  width: 40px;
  text-align: center;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  padding: 0 5px;
  z-index: 10;
  &:focus {
    outline: none;
  }
  &.prev {
    left: -20px;
    padding-left: 20px;
  }
  &.next {
    right: -20px;
    padding-right: 20px;
  }
}