.l-footer {
  margin: 40px auto 0;
  position: relative;
  .totop {
    width: 35px;
    height: 35px;
    display: block;
    position: absolute;
    background-color: $color-main;
    color: #fff;
    border-radius: 50%;
    right: 10px;
    top: -18px;
    z-index: 1;
    @include font-line(12, 35);
    text-align: center;
  }
  &__inner {
    padding: 0;
    background: #cdead1;
  }
  &__company {
    padding: 30px 20px 15px;
    text-align: center;
  }
  &__description {
    @include font-line(15, 15);
    margin: 0 0 10px;
  }
  &__logo {
    width: 73px;
    height: 73px;
    background: #fff;
    border-radius: 50%;
    padding: 15px;
    margin: auto;
    img {
      width: 43px;
      height: 43px;
    }
  }
  &__tel {
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    .left {
      flex: auto;
    }
    .freeDial {
      a {
        @include font-line(26, 33);
        font-family: 'Arial', sans-serif;
        color: $color-text01;
        text-decoration: none;
        font-weight: 700;
      }
      i {
        @include font-line(23, 32);
        position: relative;
        top: -3px;
        vertical-align: middle;
        margin-right: 5px;
      }
    }
    .right {
      font-size: 0;
      flex: 0 0 80px;
      text-align: right;
    }
  }
  &__sns {
    display: flex;
    justify-content: space-between;
    width: 70px;
    .snsIcon {
      border-radius: 50%;
      height: 33px;
      width: 33px;
      @include font-line(20, 20);
      color: #fffbf1;
      text-align: center;
      text-decoration: none;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      top: -1px;
      &__insta {
        background-color: #875024;
      }
      &__fb {
        background-color: #2f3c75;
      }

    }
  }
  &__companyinfo {
    padding: 0 20px;
    @include font-line(12, 12);
    .businessHours {
      padding-bottom: 3px;
    }
  }
  &__btnBlock {
    padding: 10px 10px 20px;
    .buttonLogin {
      width: 100%;
      .if {
        padding-right: 10px;
        position: relative;
        top: 1px;
      }
    }
  }
  &__menu {
    width: 100%;
    border-collapse: collapse;
    background: #fff;
    tr {
      height: 41px;
    }
    td {
      width: 50%;
      border: 1px solid #111111;
      position: relative;
      padding: 0 10px;
      &::after {
        @include font-line(10, 41);
        content: '\f105';
        font-family: "Font Awesome 5 free";
        font-weight: 700;
        position: absolute;
        top: 0;
        right: 10px;
      }
      a {
        display: block;
        text-decoration: none;
      }
      .ja {
        @include font-line(12, 41);
        font-weight: 700;
        color: $color-text01;
      }
    }
    .left {
      border-left: 0;
    }
    .right {
      border-right: 0;
    }

  }
  &__bottom {
    padding: 0 0 60px;
    .copy {
      @include font-line(10, 10);
      font-weight: 700;
      text-align: center;
      padding-top: 14px;
    }
  }
  &__device {
    padding: 8px 0;
    background: #fffaf3;
    display: flex;
    justify-content: center;
    &__sp {
      width: 72.5px;
      height: 25px;
      border-radius: 12.5px 0 0 12.5px;
      background: $color-accent;
      color: #fff;
      @include font-line(12, 12);
      font-weight: 700;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
    &__pc {
      width: 72.5px;
      height: 25px;
      border-radius: 0 12.5px 12.5px 0;
      background: #fff;
      color: $color-text01;
      @include font-line(12, 12);
      font-weight: 700;
      margin: 0;
      border: none;
    }
  }
}
.l-bottomfixed {
  background-color: $color-primary;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  a {
    border-left: 1px solid #fff;
    flex: 0 0 25%;
    height: 50px;
    font-weight: 700;
    @include font-line(12, 12);
    color: #fff;
    text-decoration: none;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    i,
    .text {
      display: block;
    }
    i {
      @include font-line(16, 16);
      margin: 0 0 5px;
    }
    .text {
      @include font-line(12, 12);
      display: block;
    }
    img {
      width: 18px;
      height: 18px;
      margin: 0 0 5px;
    }
    &:first-child {
      border-left: none;
    }
  }
}
