/* clearfix
--------------------------------------- */
.clearfix {
  zoom: 1;

  &::after {
    content: "";
    display: block;
    clear: both;
  }
}

/* PC / SP 出しわけ
--------------------------------------- */
.pcNone {
  @include is-pc {
    display: none !important;
  }
}

.spNone {
  @include is-mobile {
    display: none !important;
  }
}

