// ---------------------------------------------------------------
//  media query
//
// > usage
// > @include is-pc {};
// > @include is-mobile {};
//
// ---------------------------------------------------------------
@mixin is-pc {
  @media only screen and (min-width: $breakpoint-pc) {
    @content;
  }
}
@mixin is-mobile {
  @media only screen and (max-width: $breakpoint-sp) {
    @content;
  }
}
