.c-list {
  margin: 0;
  padding: 0;
  list-style: none;
  &__item {
    padding: 0 0 0 15px;
    list-style: none;
    @include font-line(10, 20);
    color: $color-text01;
    position: relative;
    &::before {
      content: "";
      background-color: $color-accent;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      margin: auto;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
    }
    &::after {
      content: "";
      background-color: #fff;
      border-radius: 50%;
      width: 4px;
      height: 4px;
      margin: auto;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 2px;
    }
  }
}

.c-dl {
  border-top: 1px solid #d6cfc8;
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: row wrap;
  &__heading,
  &__data {
    border-bottom: 1px solid #d6cfc8;
    @include font-line(10, 14);
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  &__heading {
    background-color: #fff;
    flex: 0 0 35%;
    padding: 7.5px 0 7.5px 14px;
    font-weight: 700;
    align-items: baseline;
  }
  &__data {
    background-color: #fff;
    flex: 0 0 65%;
    margin: 0;
    padding: 7.5px 20px 7.5px 0;
    font-weight: 500;
  }
}
