.p-quickSearch {
  background-color: #cdead1;
  padding: 20px 0 30px;
  position: relative;
  border-top: 3px solid $color-main;
  border-bottom: 3px solid $color-main;
  &__inner {
    padding: 0 10px;
  }
  &__heading {
    margin-bottom: 0;
    &__icon {
      width: 17px;
      height: 18.5px;
      position: relative;
      top: -2px;
      margin-right: 5px;
    }
  }

  &__type {
    padding: 10px 13px 2px;
    background: #fff;
    border-radius: 5px;
    margin-bottom: 5px;
  }
  &__titleType {
    @include font-line(13, 13);
    font-weight: 700;
    margin: 0 auto;
    text-align: center;
    position: relative;
    width: 60px;
    &:before,
    &:after {
      content: '';
      width: 10px;
      height: 0;
      border-top: 2px solid #000;
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
    }
    &:before {
      left: 0;
    }
    &:after {
      right: 0;
    }
  }
  &__bodyType {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 1px;
    position: relative;
  }
  &__item {
    @include font-line(10, 10);
    flex: 0 0 50%;
    padding: 7px 12px;
    input {
      vertical-align: text-bottom;
    }
  }
  &__price,
  &__area,
  &__station {
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
  }
  &__title {
    @include font-line(13, 30);
    font-weight: 700;
    margin: 0;
    flex: 0 0 15%;
  }
  &__bodyPrice,
  &__bodyArea,
  &__bodyStation {
    flex: 0 0 85%;
    margin-top: 2px;
    select {
      border: none;
    }
  }
  &__bodyPrice {
    display: flex;
    .separate {
      @include font-line(10,28);
      color: #553c0d;
      flex: 0 0 30px;
      text-align: center;
    }
  }
  &__button {
    margin-top: 14px;
    .button {
      width: 100%;
      .i01 {
        @include font-line(15);
        padding-left: 10px;
      }
      .i02 {
        @include font-line(20);
        padding-left: 10px;
      }
    }
  }
  &__map {
    background: url(../img/bg_home_map.png) no-repeat;
    background-size: 100%;
    width: 280px;
    height: 234px;
    margin: auto;
    position: relative;
  }
  &__chuugakkou {
    background-color: #23ac38;
    color: #fff;
    text-align: center;
    padding: 0 4px;
    line-height: 17px;
    font-size: 12px;
    border-radius: 9px;
    display: inline-block;
    position: absolute;
    &.sikata {
      left: 45px;
      top: 7px;
    }
    &.ryoso {
      left: 165px;
      top: 20px;
    }
    &.kanki {
      left: 65px;
      top: 59px;
    }
    &.yamate {
      left: 220px;
      top: 74px;
    }
    &.hioka {
      left: 95px;
      top: 99px;
    }
    &.ryonan {
      left: 161px;
      top: 109px;
    }
    &.kakogawa {
      left: 40px;
      top: 125px;
    }
    &.befu {
      left: 119px;
      top: 159px;
    }
    &.hiraoka {
      left: 192px;
      top: 162px;
    }
    &.chubu {
      left: 37px;
      top: 173px;
    }
    &.hiraokaminami {
      left: 146px;
      top: 206px;
    }
    &.hamanomiya {
      left: 45px;
      top: 213px;
    }
  }
}

