.p-toggleBox {
  display: none;
  width: 100%;
  padding-top: 30px;
  p {
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: left;
    a {
      color: #333;
      padding: 12px 4%;
      display: block;
      border-bottom: 1px #eee solid;
      text-decoration: none;
      font-size: 0.95em;
    }
  }
}