.c-button01 {
  $button01: &;
  background-color: #fff;
  border-width: 2px;
  border-style: solid;
  margin: 0 auto;
  font-weight: 700;
  @include font-line(10, 10);
  color: #fff;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;

  &--xl {
    height: 50px;
    border-radius: 25px;
    @include font-line(12.5, 12.5);
  }
  &--l {
    height: 40px;
    border-radius: 20px;
    @include font-line(16, 16);
  }
  &--m {
    height: 30px;
    border-radius: 15px;
    @include font-line(14, 14);
  }
  &--s {
    height: 28px;
    border-radius: 14px;
    @include font-line(12, 12);
  }

  &--main {
    background-color: $color-main;
    border-color: $color-main;
    color: #fff;
    &--w {
      background-color: #fff;
      border-color: $color-main;
      color: $color-text01;
    }
  }

  &--sub {
    background-color: $color-sub;
    border-color: $color-sub;
    color: #fff;
    &--w {
      background-color: #fff;
      border-color: $color-sub;
      color: $color-text01;
    }
  }

  &--accent {
    background-color: $color-accent;
    border-color: $color-accent;
    color: #fff;
    &--w {
      background-color: #fff;
      border-color: $color-accent;
      color: $color-text01;
    }
  }

  &--bullet {
    &::after {
      @include font-line(14, 14);
      content: "\f105";
      font-family: "Font Awesome 5 free";
      font-weight: 700;
      position: relative;
      display: inline-block;
      margin: 0 0 0 10px;
    }
  }

  &:focus {
    outline: none;
  }
}

.c-button02 {
  $button02: &;
  background-color: #fff;
  border: 1px solid $color-accent;
  margin: 0 auto;
  font-weight: 700;
  color: $color-text01;
  box-shadow: 1.89px 1.677px 1.32px 0.68px #f8b52b;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;

  &--xl {
    height: 40px;
    border-radius: 20px;
    @include font-line(16, 16);
  }

  &--l {
    height: 40px;
    border-radius: 20px;
    @include font-line(16, 16);
  }

  &--bullet {
    &::after {
      @include font-line(14, 14);
      content: "\f105";
      font-family: "Font Awesome 5 free";
      font-weight: 700;
      position: relative;
      display: inline-block;
      margin: 0 0 0 10px;
    }
  }

  &:focus {
    outline: none;
  }
}
