.page-home {
  #content {
    background: #fffaf3;
    padding-top: 0;
  }
  .kv {
    //background: url("../img/bg_home_kv.png") no-repeat center center;
    background-size: cover;
    width: 100%;
    //height: 323px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-bottom: 10px;
    &__inner {
      background: rgba(255, 255, 255, .6);
      width: 187px;
      height: 180px;
      margin: auto;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      text-align: center;
      padding-top: 12.5px;
      .text {
        font-family: "M PLUS Rounded 1c";
        font-weight: 700;
        @include font-line(18, 30);
        color: $color-main;
        margin: 8px 0 4px;
        &__upper {
          text-align: left;
          display: block;
          padding-left: 15px;
        }
        &__lower {
          text-align: right;
          display: block;
          padding-right: 15px;
        }
      }
      img {
        width: 90px;
        height: 89px;
      }
    }
    &__balloon {
      background: $color-sub;
      width: 81px;
      height: 81px;
      position: absolute;
      border-radius: 50%;
      top: 130px;
      left: -195px;
      right: 0;
      margin: auto;
      transform: rotate(-6deg);
      -webkit-transform: rotate(-6deg);
      &:before,
      &:after {
        content: '';
        border-bottom: 4px solid $color-primary;
        border-radius: 2px;
        width: 15px;
        position: absolute;
        transform: rotate(68deg);
        -webkit-transform: rotate(68deg);
      }
      &:before {
        left: 17px;
        top: 2px;
      }
      &:after {
        left: 10px;
        top: 6px;
      }
      .text {
        font-family: "M PLUS Rounded 1c";
        @include font-line(12, 21);
        text-align: center;
        color: #fff;
        padding: 0 0 0;
        margin-top: 12px;
        &--big {
          @include font-line(15, 0);
          position: relative;
          top: 4px;
          &::after {
            content: '・・・';
            position: absolute;
            top: 0;
            left: 0;
          }
        }
        strong {
          @include font-line(15, 15);
          font-weight: 500;
        }
      }
    }
  }

  .quo {
    text-align: center;
    margin-bottom: 10px;
    a  {
      border-bottom: 0;
    }
  }

  .introduction {
    padding: 0 2%;
    margin-bottom: 16px;
    .sum {
      width: 100%;
      display: flex;
      justify-content: space-between;
      .boardCommon {
        flex: 0 0 100px;
        position: relative;
        &:nth-child(1) {
          padding-right:1%;
        }
        &:nth-child(2) {
          flex: 1 0 100px;
        }
        &:nth-child(3) {
          padding-left:1%;
        }
        .notice {
          text-align: center;
          @include font-line(12, 12);
          margin-top: 10px;
          white-space: nowrap;
        }
        .inside {
          background-color: $color-sub;
          border-radius: 83px;
          width: 100%;
          height: 100px;
          font-weight: 700;
          color: #fff;
          display: flex;
          flex-flow: column wrap;
          justify-content: center;
          align-items: center;
          position: relative;
          .type {
            margin: 0 auto 2px;
            @include font-line(12, 12);
          }
          .number {
            @include font-line(36, 36);
            font-family: 'Roboto', sans-serif;
            display: flex;
            justify-content: center;
            align-items: baseline;
            //padding-left: 23px;
            .sub {
              margin-left: 5px;
              @include font-line(12, 12);
            }
          }
          .state {
            margin: 4px auto 0;
            @include font-line(12, 12);
          }
          &__member {
            background: $color-main;
            margin-top: 37px;
            position: relative;
            &:before {
              content: '';
              border: 6px solid $color-sub;
              border-radius: 6px;
              width: 70%;
              position: absolute;
              top: -6px;
              left: 0;
              right: 0;
              margin: 0 auto;
            }
            &:after {
              content: '';
              border: 8px solid $color-sub;
              border-radius: 8px;
              width: 0;
              height: 26px;
              position: absolute;
              top: -22px;
              left: 0;
              right: 0;
              margin: 0 auto;
            }
          }
          &__general,
          &__store {
            margin-top: 57px;
          }
        }
      }
    }
  }
  .news h2{
    position: relative;
  }
  .news {
    flex: 0 0 470px;
    padding: 26px 0 4px 10px;
    background-color: #fff;
    &__heading {
      &__icon {
        margin-left: 40px;
        &::before {
          content: '';
          background: url(../img/icon_home_news.png) no-repeat;
          width: 32px;
          height: 53px;
          position: absolute;
          left: 88px;
          bottom: 0px;
        }
      }
    }
    &__heading__sub{
      margin-left: 10px;
    }
    &List {
      width: 100%;
      &__item {
        border-bottom: 1px dotted #a0a0a0;
        margin: 0 18px 0 0;
        padding: 15px 0;
        display: flex;
        &:first-child {
          padding-top: 0;
        }
      }
      &__date {
        font-family: "Arial", sans-serif;
        font-weight: 400;
        @include font-line(15, 24);
        letter-spacing: 0.01em;
      }
      &__text {
        margin-left: 40px;
        @include font-line(15, 24);
        letter-spacing: 0.08em;
      }
    }
  }


  .information {
    width: 100%;
    margin: 0 auto;
    padding: 0;
    .box {
      width: 100%;
      height: 25px;
      margin: 0;
      display: flex;
    }
    &__heading {
      background-color: #41210f;
      width: 35%;
      margin: 0;
      padding: 0;
      font-family: "M PLUS Rounded 1c";
      font-weight: bold;
      @include font-line(10, 10);
      color: #fff;
      letter-spacing: 1.5px;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 14px;
        height: 14px;
        margin-right: 5px;
      }
    }
    &__text {
      background-color: #fff;
      width: 65%;
      height: 100%;
      margin: 0;
      padding: 0;
      .marquee {
        width: 100%;
        height: 100%;
        @include font-line(10, 10);
        color: $color-text01;
        white-space: nowrap;
        display: flex;
        align-items: center;
        overflow: hidden;
      }
    }
  }

  .bukkenlist {
    margin: 0 auto;
    padding: 30px 10px 0;
    .tabs {
      $this: ".tabs";
      width: 100%;
      text-align: center;
      display: flex;
      justify-content: space-between;
      padding: 0;
      margin: 0 0 13px;
      &__item {
        border-top: 1px solid #e5e5e5;
        border-bottom: 1px solid #e5e5e5;
        border-right: 1px solid #e5e5e5;
        background: #fff;
        flex:0 0 25%;
        &:first-child {
          border-left: 1px solid #e5e5e5;
        }
        .btn {
          height: 59px;
          @include font-line(12, 18);
          font-weight: 700;
          border: none;
          background: none;
          padding-bottom: 7px;
          .if {
            @include font-line(20, 20);
            display: block;
            padding: 10px 0 3px;
          }
        }
        &.is-active {
          .btn {
            font-weight: 700;
            border: none;
            background: none;
            outline: none;
            width: 100%;
            position: relative;
            &::after {
              background-color: $color-main;
              content: "";
              position: absolute;
              bottom: 0;
              left: 0;
              width: 100%;
              height: 4px;
              z-index: 1;
            }
          }
        }
      }
    }
    .bukken {
      margin: 0;
      padding: 0;
      display: flex;
      flex-flow: row wrap;
      justify-content: space-between;
      &__item {
        margin: 0 0 11px;
      }
    }
    .buttonBlock {
      width: 100%;
      margin: 10px 0 0;
      padding: 0;
      .button {
        width: 100%;
      }
    }
  }

  .pickup {
    &__heading {
      &__icon {
        width: 23px;
        height: 21.5px;
        position: relative;
        top: -4px;
        margin-right: 5px;
      }
    }
  }

  .pricedown {
    &__heading {
      &__icon {
        width: 31px;
        height: 26.5px;
        position: relative;
        top: -4px;
        margin-right: 5px;
      }
    }
  }

  .merit {
    background: #cdead1;
    margin: 40px auto;
    padding: 20px 10px 49px;
    position: relative;
    &__heading {
      &__inner {
        @include font-line(22.5, 32);
        margin-bottom: -6px;
      }
    }
    &__list {
      margin: 0 auto;
      padding: 0;
      display: flex;
      flex-flow: row wrap;
      justify-content: space-between;
      margin-right: 2px;
      &__item {
        border: 1px solid #9d9d9d;
        box-shadow: 2px 3px 0 0 $color-main;
        background: #fff;
        flex: 0 0 47.655%;
        text-align: center;
        margin: 0 0 35px;
        padding: 0;
        position: relative;
        .number {
          margin: auto;
          font-weight: 700;
          @include font-line(11, 11);
          color: #fff;
          font-family: "M PLUS Rounded 1c";
          width: 49px;
          height: 49px;
          border-radius: 50%;
          background: $color-main;
          position: absolute;
          top: -12px;
          left: 5px;
          z-index: 1;
          display: flex;
          flex-flow: column wrap;
          justify-content: center;
          align-items: center;
          strong {
            @include font-line(22, 22);
          }
        }
        .title {
          margin: 0;
          padding: 0 0 13px;
          font-weight: 700;
          font-family: "M PLUS Rounded 1c";
          @include font-line(13, 20);
          color: #1a8a2b;
          display: flex;
          height: 44px;
          width: 100%;
          justify-content: center;
          align-items: center;
        }
        .point {
          padding: 18px 10.5px 13px;
        }
        .character {
          position: absolute;
          width: 55px;
          height: 50.5px;
          top: -51px;
          right: 0;
        }
      }
    }
    &__button {
      width: 100%;
      margin: 0 auto;
      .button {
        width: 100%;
        @include font-line(14, 14);
      }
    }
  }

  .voice {
    margin: 0 auto 40px;
    padding: 0 10px;
    &__heading {
      &__icon {
        width: 21px;
        height: 25px;
        position: relative;
        top: -4px;
        margin-right: 5px;
      }
    }
    &__list {
      margin: 0 0 15px;
      padding: 0;
      display: flex;
      justify-content: space-between;
    }
    &__card {
      flex: 0 0 49%;
      color: #333;
      text-decoration: none;
      border: 1px solid #a0a0a0;
      border-radius: 5px;
      padding: 5px;
    }
    &__photo {
      width: 100%;
      margin: 0 auto 10px;
      position: relative;
      &::after {
        content: '';
        display: block;
        padding-top: 100%;
      }
      img {
        object-fit: cover;
        //font-family: 'object-fit: cover;';
        height: 100%;
        width: 100%;
        border-radius: 50%;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
      }
    }
    &__title,
    &__name {
      margin: 0 auto;
      width: 139px;
      font-weight: 700;
      @include font-line(12, 20);
      text-align: left;
      letter-spacing: -0.04em;
    }
    &__name {
      font-weight: 500;
    }
    &__button {
      width: 100%;
      margin: 0 auto;
      .button {
        width: 100%;
      }
    }
  }

  .shop {
    margin: 0 auto;
    padding: 0;
    &__heading {
      position: relative;
      padding-left: 30px;
      margin: 0 10px 20px;
      &::before {
        content: '';
        background: url(../img/icon_home_shop.png) no-repeat;
        background-size: cover;
        width: 25.5px;
        height: 22px;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
    &__photo {
      width: 100%;
      margin: 0 auto;
      img {
        width: 100%;
        height: auto;
      }
    }
    &__button {
      margin: 12.5px 10px 0;
      padding: 0;
      text-align: center;
      .groupButton {
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
      }
      .button {
        &Reserve {
          flex: auto;
        }
        &Map {
          flex: 0 0 75px;
          text-align: left;
          margin-left: 10px;
          @include font-line(12,12);
          padding-right: 15px;
          color: $color-main;
          position: relative;
          &:after {
            content: '';
            background: url(../img/icon_home_mapmarker.png) no-repeat;
            background-size: cover;
            width: 19.5px;
            height: 23.5px;
            position: absolute;
            right: 6px;
            top: 0;
            bottom: 0;
            margin: auto;
          }
        }
        &Event {
          width: 100%;
          margin: 12.5px auto 0;
        }
      }
    }
  }
}
